import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { 
  DORMITORY_ROOM, 
  DORMITORY_BUILDING,
} from 'routes/data/category';
import { InfoCard } from 'components/common';
import {
  getQuickLinkDormNo,
  getQuickLinkDormRoomNo,
} from 'services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    ...DORMITORY_BUILDING,
    service: getQuickLinkDormNo,
  },
  {
    ...DORMITORY_ROOM,
    service: getQuickLinkDormRoomNo,
  },
];

class DashboardCategory extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardCategory {...props} />
  </Page>
);



